import React from 'react';
import HistoryTwo from './history_two';
import { BrowserRouter } from 'react-router-dom';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
// from node modules


class FormFour extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            url: 'url.com',
            method: 'click a method button',
            showMessage: false,
            showHistory: true,
            loading: false,
            pokemon: [],
            history: [],
            body: [],
            showHideBod: false,
            json: '',
            miniVerb: ''
        }
    }

    handleMini = (x, y) =>{
        this.setState({ miniVerb: x });
        this.setState({ url: x });
        this.setState({ method: y });
        this.setState({ showMessage: false });
        console.log('Our new Mini: ', this.state.miniVerb);
        console.log('Our new verb: ', this.state.method);
        document.getElementById("path").value = x;
      }

    componentDidMount() {
        console.log('this is our mini VERB', this.state.miniVerb);
        // const history = localStorage.getItem(JSON.parse('history'));
        this.setState({
            history: JSON.parse(localStorage.getItem('history'))
        });

    }


    render() {
        return (
            <div className='about-div' id='container-div-2'>
                <div className='form-div-2'><h1 id='history-info'>Browse your history and go back to main page on a click.</h1>
                <p>(All i have left to do is make these links to pass state but they work!)</p>
                </div>
                <div id='history-div'>
                    <div><h2 id='history-head'> History</h2>
                    
                    </div>
                    <HistoryTwo handleMini={this.handleMini} {...this.state} />
                </div>

            </div>

        )
    }
}

export default FormFour;